<template>
    <div class="content">
        <h3>欢迎来到Rich的博客</h3>
        <div class="block">
            <el-avatar :size="50" :src="user.avatar"></el-avatar>
            <div>{{ user.username }}</div>
        </div>

        <div class="action">
            <el-link href="/blogs">主页</el-link>
            <el-divider direction="vertical"></el-divider>
            <span><el-link type="success" href="/blog/add" :disabled="!hasLogin">发表博客</el-link></span>

            <el-divider direction="vertical"></el-divider>
            <span v-show="!hasLogin">
                <el-link type="primary" @click="login">登录</el-link>
            </span>

            <span v-show="hasLogin">
                <el-link type="danger" @click="logout">退出</el-link>
            </span>
        </div>
    </div>
</template>

<script>
export default {
    name: "Header",
    data() {
        return {
            hasLogin:false,
            user: {
                username: '请先登录',
                avatar: ''
            },
            blogs: {},
            currentPage: 1,
            total: 0
        }
    },
    methods: {
        //退出操作
        logout () {
            const _this = this
            //首先调用后端logout接口(因该接口需要认证权限,所以需要传入token)
            //其次调用$store清除用户信息及token
            _this.$axios.get('http://localhost:8081/logout', {
                headers: {
                    "Authorization": localStorage.getItem("token")
                }
            }).then(res => {
                _this.$store.commit("REMOVE_INFO")
                _this.$router.push("/login")
            })
        },
        login () { //跳转登录页面进行登录
            this.$router.push("/login")
        }
    },
    created() {
        if(this.$store.getters.getUser.username) {
            this.user.username = this.$store.getters.getUser.username
            this.user.avatar = this.$store.getters.getUser.avatar
            this.hasLogin = true
        }
    }
}
</script>

<style scoped>

.content {
    max-width: 960px;
    margin: 0 auto;
    text-align: center;
}

.action {
    margin: 10px 0;
}
</style>
