<template>
    <div>
        <el-container>
            <div class="loginForm">
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm"
                         class="demo-ruleForm">
                    <p>博客登录</p>
                    <span>
                        <router-link to="/blogs">
                        <img class="logo" src="https://cdn.jsdelivr.net/gh/OriginalCoder0/gallery@master/images/1625926121977-docker.png">
                    </router-link>
                    </span>
                    <el-form-item prop="username">
                        <el-input placeholder="请输入用户名" type="text" maxlength="12" v-model="ruleForm.username"></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input placeholder="请输入密码" type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                        <el-button @click="resetForm('ruleForm')">重置</el-button>
                    </el-form-item>
                </el-form>
                <div class="copy" style="color:#ccc">&copy;Rich 2021</div>
            </div>
        </el-container>
    </div>
</template>
<script>
export default {
    name: 'Login',
    data() {
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            } else {
                callback();
            }
        };
        return {
            ruleForm: {
                username: '',
                password: '',
            },
            rules: {
                password: [
                    {validator: validatePass, trigger: 'blur'}
                ],
                username: [
                    {required: true, message: '请输入用户名', trigger: 'blur'},
                    {min: 3, max: 12, message: '长度在 3 到 12 个字符', trigger: 'blur'}
                ]
            }
        };
    },
    methods: {
        submitForm(formName) {
            const _this = this;
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 提交逻辑
                    this.$axios.post('http://localhost:8081/login', this.ruleForm).then((res) => {
                        // console.log(res.headers);
                        // console.log(res);
                        // const userInfo = res.data.data;
                        // console.log(userInfo);
                        const token = res.headers['authorization']
                        //存储(共享)全局变量jwt和userInfo
                        _this.$store.commit('SET_TOKEN', token)
                        _this.$store.commit('SET_USERINFO', res.data.data)
                        _this.$router.push("/blogs")
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        }
    },
    mounted() {
        this.$notify({
            title: 'Rich',
            message: '关注CSDN，【王菜鸟】，可以获得更多学习资源喔~',
            duration: 2000
        });
    }
}
</script>

<style scoped>
.el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
}

.el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
}

.loginForm {

    width: 380px;
    box-shadow: 0 6px 20px 5px rgba(40, 120, 255, 0.1), 0 16px 24px 2px rgba(0, 0, 0, 0.05);
    background: #fff;
    padding: 20px;
    margin: 20% auto;
    text-align: center;
    border-radius: 20px;

}

body > .el-container {
    margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
    line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
    line-height: 320px;
}

.loginForm p{
    color: #1c8adb;
    font-size: 20px;
    font-weight: bold;
    margin: 8px 0;
}
.copy{
    text-align: center;
    font-size: 15px;
}

.logo{
    width: 92px;
    height: 85px;
    border-radius: 43px;
    margin: 5px 0;
}

.demo-ruleForm {
    max-width: 500px;
    margin: 0 auto;
}
</style>
