<template>
    <div>
        <Header></Header>
        <div class="blog">
            <h2 class="title">{{ blog.title }}</h2>
            <el-link  v-if="ownBlog" class="btn-margin">
                <el-button type="primary" icon="el-icon-edit">
                    <router-link class="alink" :to="{name:'BlogEdit',params:{blogId:blog.id}}">
                        编辑
                    </router-link>
                </el-button>
            </el-link>
                <el-button icon="el-icon-delete"  v-if="ownBlog" type="danger" @click="delBlog">删除</el-button>
            <el-divider></el-divider>
            <div class="markdown-body" v-html="blog.content"></div>

        </div>
        <Footer></Footer>
    </div>
</template>

<script>
import 'github-markdown-css'
import Header from "../components/Header";
import Footer from "@/components/Footer";

export default {
    name: "BlogDetail",
    components: {Header,Footer},
    data() {
        return {
            blog: {
                id: '',
                title: '',
                content: '',
                description: ''

            },
            ownBlog: false
        }
    },
    methods: {
        delBlog() {
            const blogId = this.$route.params.blogId
            const _this = this
            if (blogId) {
                this.$confirm('此操作将永久删除该文章, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    _this.$axios.delete('/blogDel/'+ blogId, {
                        headers: {
                            "Authorization": localStorage.getItem("token")
                        }
                    }).then(() => {
                        this.$message({
                            type: 'success',
                            message: "删除成功！"
                        });
                        _this.$router.push("/blogs")
                    })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });


            }
        }
    },
    created() {
        //获取动态路由的 blogid
        const blogId = this.$route.params.blogId
        const _this = this
        if (blogId) {
            this.$axios.get("/blog/" + blogId).then(res => {
                const blog = res.data.data
                _this.blog.id = blog.id
                _this.blog.title = blog.title
                _this.blog.description = blog.description

                //MardownIt 渲染
                var MardownIt = require("markdown-it")
                var md = new MardownIt();
                var result = md.render(blog.content)
                _this.blog.content = result
                //查看是否是登录人 是则可以编辑
                _this.ownBlog = (blog.userId === _this.$store.getters.getUser.id)
            })
        }

    }
}
</script>

<style scoped>
.blog {
    margin: 20px 0;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    width: 100%;
    min-height: 700px;
    padding: 20px;
}

.title {
    text-align: center;
}
.btn-margin{
    margin-right: 20px;
}
.alink{
    text-decoration: none;
    color: #fff;
}
</style>
